import Vue from "vue";
//import Component from "vue-class-component";
import FInput from "@/components/input/form.vue";
import FList from "@/components/output/list.vue";
export default Vue.extend({
    name: "Root",
    components: {
        FInput: FInput,
        FList: FList
    },
    data: function () {
        return {
            tasks: [],
            t: ""
        };
    },
    created: function () { },
    methods: {
        addTask: function () {
            if ("" != this.t) {
                this.tasks.push(this.t);
                this.t = "";
            }
        }
    }
});



